/**
 * Color & Background
 *
 */

$primary-color: #121429 !default;
$secondary-color: #6D7F99 !default;
$white-color: #FFFFFF;
$black-color: #000000;
$yellow-color: #F5E40E;
$orange-color: #FD5334;
$blue-color: #1A9DE7;
$blue-sea-color: #50E3C2;
$pink-color: #EA5771;
$green-color: #039245;
$red-color: #D22D36;
$yellow2-color: #F2C94C;
$blue2-color: #008FFF;
$purple-color: #CCD6F6;
$gray-color: #8892b0;
$green-tint: rgba(100, 255, 218, 0.1);


/**
 *  Font & Text
 *
 */

// font-family
$font-family-primary: "Fira Code", "Fira Mono", "Roboto Mono", monospace;
$font-family-secondary : 'Open Sans', sans-serif;
$font-family-third : 'Kanit', sans-serif;


// font-size
$font-size-h1: 80px;
$font-size-h2: 32px;
$font-size-h3: 28px;
$font-size-h4: 20px;
$font-size-h5: 17px;
$font-size-h6: 14px;
$font-size-h7: 13px;
$font-size-h8: 12px;


// font weight

$font-weight-primary: 700;
$font-weight-secondary: 600;
$font-weight-third: 500;
$font-weight-fourth: 400;
$font-weight-fifth: 300;
$font-weight-six: 200;
//$font-weight-primary: 900;
//$font-weight-secondary: 800;


// 600 


// letter-spacing
$letter-spacing-primary: 1.2px;
